import React from 'react';
import '../Bio/bio.css';

const Bio = ({biodata}) => {
    return (
    <div class='container'>
    
    
<div class="card-container">
    <span class="pro">PRO</span>
    <img src={biodata.profile_image} alt="user"/>
    <h3><b>{biodata.name}</b></h3>
    <h6>{biodata.designation}</h6>
    <div><b>{biodata.department}</b> of<br/> <b>{biodata.college}</b></div>
    <div class="buttons">
        <button class="primary">
            Message
        </button>
        <button class="primary ghost">
            Following
        </button>
    </div>
    <div class="skills">
        <h6>Skills</h6>
        <ul>
            {biodata.skills.map(skill=>(
                <li>{skill}</li>
            ))}
        </ul>
    </div>
</div>
    </div>
    
        
    )
}

export default Bio;