import React from 'react';
import '../Contact/contact.css';

const Contact = ({contactinfo}) => {

    

    return (
        <div class='containerC'>
          <div>
            {contactinfo.social_links.map(link=>(
              Object.keys(link).map((key,index)=>(
                <a href={link[key]}><button class='contactButton'>{key}</button></a>
              ))
            ))}
          
            <a href={'mailto:'+contactinfo.email}><button class='contactButton'>Email Me</button></a>
          </div>
        </div>
    )
}

export default Contact;