import React from 'react';
import '../OngoingProjects/lab.css';

const OngoingProjects = ({projects}) => {
    return (
        <div class='labContainer'>
            <b>üôè <u>My Ongoing Projects</u> :</b>
            <br/><br/><br/>
          {projects.ongoing_projects.length===0 ? (<div><ul><span>No Ongoing Projects Found ‚ùå</span></ul></div>) : (
        <ol>
        {projects.ongoing_projects.map((project) => (
          Object.keys(project).map(
            (key, index) => (
              <li class='project'><a>{key}</a><span className='live'> üî¥</span></li>
              
            )
          )
        ))}
        </ol> ) }
        <br/>
        <div id='projects'></div>
        </div>
    )
}

export default OngoingProjects;