import React from 'react';
import '../Projects/store.css';
 
const Projects = ({projects}) => {
    return (
        <div class='containerS'>
           <b>üôè <span style={{transform:`rotate(-90deg)`,color:'#fff'}}>Some of My Projects</span> :</b>
        <div style={{marginTop: '20%'}}></div>
        <ol>
        {projects.projects.map((project) => (
          Object.keys(project).map(
            (key, index) => (
              <li><button class='storeButton'>{project[key][0].length > 0 ?<a href={project[key][0]} target="_blank">{key}</a>:<span>{key}</span>}{project[key][1].length > 0 && project[key][0].length > 0 ?<span><a href={project[key][1]} target='_blank'> üéÅ </a></span>:<span> ‚úîÔ∏è</span>}</button></li>
            )
          )
        ))}
        </ol>
        </div>
    )
}

export default Projects;